<template>
  <div class="page trialpreson flex-v totalcheckdoctor">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">分析报告</div>
      <div class="head-btn"
        v-if='btns.hasInsert'
        data-btn-id='105006001'
        @click="goEditOrAdd('add')">
        <img src="@/assets/img/add.png"
          alt="新增">&nbsp;<span>新增</span>
      </div>
    </div>
    <div class="query-form">
      <el-form inline
        class="default-form-style"
        size='small'>
        <el-form-item label="创建日期：">
          <el-date-picker value-format='yyyy-MM-dd'
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="机构名称：">
          <el-select v-model="createOrganizationId"
            placeholder="全部"
            clearable>
            <el-option v-for='item in orgList'
              :key='item.sysGuid'
              :value='item.sysGuid'
              :label='item.oname' />
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn"
            icon="el-icon-search"
            @click="$refs.pagination.initPageList()"
            round>查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table-box flex-item">
      <el-table stripe
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height='100%'
        v-loading='loading'
        header-cell-class-name='custom-table-cell'>
        <el-table-column type="index"
          label="序号">
          <template v-slot="{$index}">
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="createDate"
          label="创建日期">
          <template v-slot="scope">
            {{scope.row.createDate | formatDate}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="residenceAddress"
          label="乡镇街道"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          prop="createman"
          label="创建人" />
        <el-table-column align="center"
          prop="printDate"
          label="打印时间"
          :formatter='({printDate}) => $options.filters.formatDate(printDate)' />
        <el-table-column align="center"
          prop="printNum"
          label="打印次数"
          :formatter='({printNum}) => $options.filters.emptyData(printNum, 0)' />
        <el-table-column align="center"
          label="操作">
          <template v-slot='{row}'>
            <i class="iconfont bj-edit"
              v-if="btns.hasUpdate"
              data-btn-id='105006002'
              @click="goEditOrAdd(row.reportExamId)"></i>
            <i class="iconfont bj-delete"
              v-if="btns.hasDel"
              data-btn-id='105006003'
              @click="delConfirm(row.reportExamId)"></i>
            <i class="iconfont bj-search"
              @click="goReportComplete(row.reportExamId)"></i>
          </template>
        </el-table-column>

      </el-table>
    </div>

    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="querys"
        query-url="/analyzereport/getAnalyzeReportByParam"
        @listResult="getListResult"
        ref="pagination"
        @controlLoading="loading=$event" />
    </div>
  </div>
</template>
<script>
export default {
  name: 'Analysisreport',

  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    return {
      tableData: [],
      dateRange: '',
      pageNum: 1,
      pageSize: 10,
      loading: true,
      createOrganizationId
    }
  },

  methods: {
    delConfirm(reportExamId) {
      this.$confirm('', {
        title: '确定删除分析报告？',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(e => {
          this.$post('/analyzereport/removeOneById', { reportExamId })
            .then(res => {
              this.$message.success('删除成功')
              if (this.tableData.length === 1) {
                let pageNum = this.pageNum > 1 ? this.pageNum-- : this.pageNum
                this.$refs.pagination.initPageList(pageNum)
              } else {
                this.$refs.pagination.initPageList()
              }
            })
            .catch(err => {
              this.$alert(`原因：${err}`, '删除失败', {
                showClose: false,
                customClass: 'msg-box danger'
              })
            })
        })
        .catch(cancel => cancel)
    },
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    // 编辑/新增跳转
    goEditOrAdd(id) {
      this.$router.push({
        name: 'addanalysisreport',
        query: { id }
      })
    },

    // 详情跳转
    goReportComplete(id) {
      this.$router.push({
        name: 'analysisreportDetails',
        query: { id }
      })
    }
  },
  computed: {
    querys() {
      let date = this.dateRange
      return {
        startTime: date ? `${this.dateRange[0]} 00:00:00` : '',
        endTime: date ? `${this.dateRange[1]} 23:59:59` : '',
        createOrganizationId: this.createOrganizationId
      }
    },
    btns() {
      let btnIds = this.$store.state.btnAuthorityIds
      return {
        hasInsert: ~btnIds.indexOf('105006001'), // 新增按钮权限
        hasUpdate: ~btnIds.indexOf('105006002'), // 修改按钮权限
        hasDel: ~btnIds.indexOf('105006003') // 删除按钮权限
      }
    },
    orgList() {
      return this.$store.state.orgList
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.totalcheckdoctor {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
}
</style>

